
.wrapper {
  width: 100%;
  height: 100%;
}
.wrapper ::-webkit-scrollbar {
    width: 5px;
    height: 12px;
    -webkit-border-radius: 40%;
}
.wrapper ::-webkit-scrollbar-thumb {
    background: #c3c3c3;
    -webkit-border-radius: 12px;
}
.wrapper .container {
    width: 100%;
    height: 100%;
}
.wrapper .vue-im {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    width: 100%;
    min-width: 800px;
    height: calc(100% - 61px);
    min-height: 600px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.wrapper .im-chat-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-justify: space-between;
    width: 100%;
}
.wrapper .brief {
    width: 60%;
}
.wrapper .mini {
    position: fixed;
    bottom: 30px;
    right: 40px;
    cursor: pointer;
    width: 36px;
    height: 36px;
    border-radius: 5px;
    padding: 5px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.wrapper .mini img {
      width: 100%;
      height: 100%;
}
@-webkit-keyframes twinkle {
0% {
    opacity: 1;
}
25% {
    opacity: 0;
}
50% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes twinkle {
0% {
    opacity: 1;
}
25% {
    opacity: 0;
}
50% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



